.chart-container {
  position: relative;
  display: block;
  /*background-color: rgba(255, 255, 255, 0.2);*/
  width: auto;
  height: 100%;
  /* instead padding */
  border-top: 10px solid rgba(0, 0, 0, 0);
  border-bottom: 10px solid rgba(0, 0, 0, 0);
  /* overflow: hidden;*/
}

.bars-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
      align-items: stretch;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  min-width: 100%;
  width: auto;
  /* border-left: thin dashed #0Ff;
  border-right: thin dashed #0Ff; */
  height: 100%;
}



